"use client"

import { Bell, Calendar, User, Vote, Flag } from 'lucide-react'
import { Button } from "@/components/ui/button"
import Link from "next/link"
import { motion } from "framer-motion"

export default function EmptyState({ type }) {
  const getIcon = () => {
    switch (type) {
      case "campaigns":
        return <Calendar className="h-16 w-16 text-muted-foreground/30" />
      case "votes":
        return <Vote className="h-16 w-16 text-muted-foreground/30" />
      case "registrations":
        return <User className="h-16 w-16 text-muted-foreground/30" />
      default:
        return <Bell className="h-16 w-16 text-muted-foreground/30" />
    }
  }

  const getMessage = () => {
    switch (type) {
      case "campaigns":
        return "You haven't created or participated in any campaigns yet"
      case "votes":
        return "You haven't cast or received any votes yet"
      case "registrations":
        return "You haven't registered for any campaigns yet"
      default:
        return "You don't have any notifications yet"
    }
  }

  const getActionText = () => {
    switch (type) {
      case "campaigns":
        return "Create a Campaign"
      case "votes":
      case "registrations":
        return "Browse Campaigns"
      default:
        return "Explore Campaigns"
    }
  }

  const getActionLink = () => {
    switch (type) {
      case "campaigns":
        return "/campaigns/create"
      default:
        return "/campaigns"
    }
  }

  const getSubtitle = () => {
    switch (type) {
      case "campaigns":
        return "Start your own campaign or join existing ones"
      case "votes":
        return "Participate in active campaigns to see your voting activity"
      case "registrations":
        return "Register for campaigns to start participating"
      default:
        return "Your activity will appear here once you start participating"
    }
  }

  return (
    <div className="flex flex-col items-center justify-center py-12 text-center rounded-lg border border-dashed border-muted-foreground/20 bg-muted/5">
      <div className="bg-background/80 rounded-full p-4 shadow-sm">
        {getIcon()}
      </div>
      <h3 className="mt-4 text-lg font-medium">No {type === "all" ? "activity" : type} found</h3>
      <p className="mt-1 text-sm text-muted-foreground max-w-sm">{getMessage()}</p>
      <p className="text-xs text-muted-foreground/70 mt-1 max-w-xs">{getSubtitle()}</p>
      <Link href={getActionLink()}>
        <Button className="mt-6 gap-2">
          {type === "campaigns" && <Flag className="h-4 w-4" />}
          {getActionText()}
        </Button>
      </Link>
    </div>
  )
}
